.switch-input {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch-input:checked + label {
  background: var(--brand-main-opt);
}

.switch-input:checked + .switch-lbl:after {
  width: 13px;
  height: 13px;
  left: calc(100% - 2px);
  translate: -100%;
}

.switch-lbl {
  cursor: pointer;
  width: 40px;
  height: 20px;
  background-color: var(--brand-light);
  border: 1.5px solid var(--theme-tgl-bg);
  border-radius: 20px;
  margin-right: 12px;
  display: block;
  position: relative;
}

.switch-lbl:after {
  content: "";
  width: 14px;
  height: 14px;
  background-color: var(--theme-tgl-bg);
  border-radius: 20px;
  transition: all .25s;
  position: absolute;
  top: 50%;
  left: 1.5px;
  transform: translateY(-50%);
}

